.footer {
    background: #6956af;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

article, aside, footer, header, nav, section {
    display: block;
}
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: sans-serif;
}

.footer>.bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #5138a1;
    padding: 8px;
    font-weight: 600;
}
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: sans-serif;
}

.footer>.bottom>img {
    width: 20px;
    position: relative;
    top: 3px;
    left: 2px;
}

img {
    border-style: none;
}
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: sans-serif;
}



